.box {
  height: 50vh;
  display: flex;
  perspective: 1000px;
  -webkit-perspective: 1000px;
  margin: 20px 0;
  z-index: 1;
}
.wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 236, 129);
  transform: rotateY(0);
  transition: all 1s ease;
  -webkit-transition: all 1s ease;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
}
.box:hover .wrapper {
  transform: rotateY(180deg);
}
.box:hover .backPage p {
  opacity: 1;
}
.box:hover .frontPage p {
  opacity: 0;
}
.frontPage {
  height: 100%;
  width: 100%;
  position: absolute;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.7rem;
  z-index: 1;
  transition: all .5s ease;
}
.backPage {
  height: 100%;
  width: 100%;
  position: absolute;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.7rem;
 
}
.backPage p {
  transition: all 1s ease;
  opacity: 0;
  transform: rotateY(180deg);
}
.frontPage p {
  transition: all .5s ease;
  opacity: 1;
}
